<template>
  <div class="contain_wrap">
    <div class="return" @click="returnGo">
      <van-icon name="arrow-left" />
    </div>

    <Tabs :tabs="tabs" @exTabItem="exTabItem">
      <div v-if="tabs[tabitem].isActive===true">
        <div class="item_wrap" v-for="item in userOrderData" :key="item.order_number">
          <div class="item_list" v-if="type!=0">
            <div>{{item.type==1?'待收货':'已收货'}}</div>
            <div class="item_contian">
              <van-button
                class="btn"
                plain
                size="mini"
                @click="queryOrder(item.order_id,item.type)"
              >{{item.type==1?'确认收货':'删除订单'}}</van-button>
            </div>
          </div>
          <div class="item_list">
            <div class="item_title">订单编号</div>
            <div class="item_contian oreder_num">{{item.order_number}}</div>
          </div>
          <div class="item_list">
            <div class="item_title">订单价格</div>
            <div class="item_contian oeder_price">￥{{item.order_price}}</div>
          </div>
          <div class="item_list">
            <div class="item_title">订单日期</div>
            <div class="item_contian oreder_date">{{item.time}}</div>
          </div>
        </div>
      </div>
    </Tabs>
  </div>
</template>

<script>
import { reaxios } from "@/assets/js/reaxios";
import "@/assets/css/category.css";
import Tabs from "../components/Tabs";
export default {
  data() {
    return {
      tabs: [
        {
          id: 0,
          value: "全部订单",
          isActive: true
        },
        {
          id: 1,
          value: "待收货",
          isActive: false
        },
        {
          id: 2,
          value: "已收货",
          isActive: false
        }
      ],

      // 订单数据
      userOrderData: [],
      type: 0,
      tabitem: 0
    };
  },

  created() {
    this.tabChahge(this.tabitem);
  },
  methods: {
    returnGo() {
      this.$router.push("/user");
    },
    // 数据获取
    tabChahge(index) {
      let userOrderData = JSON.parse(localStorage.getItem("goods"));
      let filOrder = [];
      if (index != 0) {
        filOrder = userOrderData.filter(v => v.type == index);
      } else {
        filOrder = userOrderData;
      }

      // 修改原数组
      this.tabs.forEach((v, i) =>
        i == index ? (v.isActive = true) : (v.isActive = false)
      );
      this.userOrderData = filOrder;
    },
    // tab切换
    exTabItem(e) {
      this.tabitem = e;
    }
  },

  // 自定义组件注册
  components: { Tabs }
};
</script>

<style lang="less" scoped>
.contain_wrap {
  padding: 30px 0;
  .return {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 95%;
    background-color: var(--themeColor);
    text-align: left;
    font-size: 24px;
    display: flex;
    align-items: center;
    color: aliceblue;
  }
  .tabs-box {
    margin: 0;
  }
  .item_wrap {
    border-bottom: 5rpx solid rgb(216, 216, 216);
    .item_list {
      display: flex;
      justify-content: space-between;
      padding: 10rpx;

      .btn {
        font-size: 26rpx;
      }

      .item_title {
        color: rgb(155, 155, 155);
        font-size: 30rpx;
      }

      .oreder_num {
        color: rgb(170, 170, 170);
      }
      .oeder_price {
        color: red;
        font-size: 34rpx;
      }
      .oreder_date {
        font-size: 28rpx;
      }
    }
  }
}
</style>


